<template>
  <div v-if="qna" class="regist">
    <h1 class="underline">QnA </h1>
    <div class="regist_form">
      <label for="isbn"></label>
      <div class="view">{{qna.isbn}}</div>
      <label for="title"></label>
      <div class="view">{{qna.title}}</div>
      <label for="author"></label>
      <div class="view">{{qna.author}}</div>
      <label for="time"></label>
      <div class="view">{{qna.time}}</div>
      <label for="content"></label>
      <div class="view" v-html="enterToBr(qna.content)"></div>
      <div style="padding-top: 15px">
        <router-link :to="`/qna/modify/${qna.isbn}`" class="btn"></router-link>
        <a href="#" class="btn" @click="deleteQna"></a>
        <router-link to="/qna" class="btn"></router-link>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters } from "vuex";
import http from "@/util/http-common";

export default {
  computed: {
    ...mapGetters(["qna"])
  },
  methods: {
    deleteQna(){
      if(confirm("?")){
        http.delete(`/qnaapi/${this.$route.params.isbn}`).then(({ data }) => {
          let msg = "   .";
          if (data === "success") {
            msg = " .";
          }
          alert(msg);
          this.$router.push("/qna");
        });
      }
    },
    numberWithCommas(x) {
      return String(x).replace(/\B(?=(\d{3})+(?!\d))/g, ",");
    },
    enterToBr(str) {
      return String(str).replace(/(?:\r\n|\r|\n)/g, "<br />");
    }
  }
};
</script>

